<template>
  <div>
    <el-button @click="add" style="margin-bottom: 20px">Add</el-button>
    <el-table :data="tableData" stripe  header-row-class-name="table-header" border>
      <el-table-column prop="teacherId" label="id" min-width="70" align="center"></el-table-column>
      <el-table-column prop="name" label="name" min-width="70" align="center"></el-table-column>
      <el-table-column prop="className" label="className" min-width="70" align="center"></el-table-column>
      <el-table-column label="Edit" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)">Edit</el-button>
        </template>
      </el-table-column>
      <el-table-column label="Delete" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="deleteData(scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="Add Teacher" :visible.sync="flag" width="25%" @close="close">
      name: <el-input v-model="form.name" style="margin-bottom: 20px"></el-input>
      className: <el-input v-model="form.className" style="margin-bottom: 20px"></el-input>
      password: <el-input v-model="form.password" style="margin-bottom: 20px"></el-input>
      <el-button @click="save">Save</el-button>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        form: {
          teacherId: null,
          className: null,
          name: null,
          password: null
        },
        flag:false
      }
    },
    components: {
    },
    mounted () {
      this.$axios.post("http://127.0.0.1:8000/findTeacher").then((response) => {
        this.tableData = response.data
      })
    },
    methods: {
      deleteData (obj) {
        this.$axios.post("http://127.0.0.1:8000/delTeacher", {teacherId: obj.teacherId}).then((response) => {
          this.tableData = response.data
        })
      },
      save () {
        this.$axios.post("http://127.0.0.1:8000/saveTeacher", this.form).then((response) => {
          if (response) {
            this.tableData = response.data
            this.$message({
              type: 'success',
              message: '保存成功'
            })
            this.form = {}
            this.flag = false
          }
        })
      },
      close () {
        this.flag = false
        this.form = {}
      },
      edit (obj) {
        this.flag = true
        this.form = JSON.parse(JSON.stringify(obj))
      },
      add () {
        this.flag = true
      }
    }
  }
</script>

<style>
  .table {
    margin-top: 30px;
  }

  .pagination {
    margin-top: 10px;
    float: right;
  }

</style>



