<template>
  <div>
    <el-button @click="add" style="margin-bottom: 20px">Add</el-button>
    <el-table :data="tableData" stripe  header-row-class-name="table-header" border>
      <el-table-column prop="parentInfoId" label="id" min-width="70" align="center"></el-table-column>
      <el-table-column prop="parPhone" label="parPhone" min-width="70" align="center"></el-table-column>
      <el-table-column label="Edit" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)">Edit</el-button>
        </template>
      </el-table-column>
      <el-table-column label="Delete" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="deleteData(scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="Add Parent" :visible.sync="flag" width="25%" @close="close">
      parentPhone: <el-input v-model="parentPhone" style="margin-bottom: 20px"></el-input>
      <el-button @click="save">Save</el-button>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        parentPhone: null,
        flag:false,
        id: null
      }
    },
    components: {
    },
    mounted () {
      this.$axios.post("http://127.0.0.1:8000/findParentInfo").then((response) => {
        this.tableData = response.data
      })
    },
    methods: {
      deleteData (obj) {
        this.$axios.post("http://127.0.0.1:8000/delParentInfo", {parPhone: obj.parPhone}).then((response) => {
          this.tableData = response.data
        })
      },
      save () {
        this.$axios.post("http://127.0.0.1:8000/saveParentInfo", {parentInfoId: this.id, parPhone: this.parentPhone}).then((response) => {
          if (response) {
            this.tableData = response.data
            this.$message({
              type: 'success',
              message: '保存成功'
            })
            this.parentPhone = null
            this.id = null
            this.flag = false
          }
        })
      },
      close () {
        this.flag = false
        this.parentPhone = null
      },
      edit (obj) {
        this.flag = true
        this.parentPhone = obj.parPhone
        this.id = obj.parentInfoId
      },
      add () {
        this.flag = true
      }
    }
  }
</script>

<style>
  .table {
    margin-top: 30px;
  }

  .pagination {
    margin-top: 10px;
    float: right;
  }

</style>



