<template>
  <div>
    <li class="el-menu-item is-active" @click="parentInfo">parentInfo</li>
    <li class="el-menu-item is-active" @click="parentStu">parentStu</li>
    <li class="el-menu-item is-active" @click="teacher">teacher</li>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import store from '@/store'
    export default {
        name: 'db-sidebar',
        data() {
            return {
            }
        },
      methods: {
        parentInfo () {
            store.dispatch('setMenu', 'parentInfo')
            store.dispatch('setNum', 0)
          },
        parentStu () {
          store.dispatch('setMenu', 'parentStu')
          store.dispatch('setNum', 0)
        },
        teacher () {
          store.dispatch('setMenu', 'teacher')
          store.dispatch('setNum', 0)
        }
      }
    }

</script>

<style scoped>
    li {
        padding-left: 20px;
    }
</style>



