<template>
  <div>
    <el-button @click="add" style="float: right">Add</el-button>
    <el-table :data="tableData" stripe header-row-class-name="table-header" border>
      <el-table-column prop="id" label="id" min-width="70" align="center"></el-table-column>
      <el-table-column prop="parPhone" label="parPhone" min-width="70" align="center"></el-table-column>
      <el-table-column prop="stuName" label="stuName" min-width="70" align="center"></el-table-column>
      <el-table-column prop="teacherId" label="teacherId" min-width="70" align="center"></el-table-column>
      <el-table-column label="Edit" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="edit(scope.row)">Edit</el-button>
        </template>
      </el-table-column>
      <el-table-column label="Delete" min-width="70" align="center">
        <template slot-scope="scope">
          <el-button @click="deleteData(scope.row)">Edit</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="Add Parent stu" :visible.sync="flag" width="30%" @close="close">
      parentPhone:<el-input v-model="form.parentPhone"></el-input>
      stuName:<el-input v-model="form.stuName"></el-input>
      teacherId:<el-select v-model="form.teacherId">
        <el-option v-for="(item, index) in teacherList" :key="index" :label="item.name" :value="item.teacherId"></el-option>
      </el-select>
      <el-button @click="save">Save</el-button>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        form: {parPhone: null, stuName: null, teacherId: null},
        flag:false,
        teacherList: []
      }
    },
    components: {
    },
    mounted () {
      this.$axios.post("http://127.0.0.1:8000/findParentStu").then((response) => {
        this.tableData = response.data
      })
      this.$axios.post("http://127.0.0.1:8000/findTeacher").then((response) => {
        this.teacherList = response.data
      })
    },
    methods: {
      deleteData (obj) {

      },
      save () {
        this.$axios.post("http://127.0.0.1:8000/saveParentStu", this.form).then((response) => {
          if (response) {
            this.$message({
              type: 'success',
              message: '保存成功'
            })
            this.tableData = response.data
            this.form = {}
            this.flag = false
          }
        })
      },
      close () {
        this.flag = false
        this.form = {}
      },
      edit (obj) {
        this.flag = true
        this.form = JSON.parse(JSON.stringify(obj))
      },
      add () {
        this.flag = true
      }
    }
  }
</script>

<style>
  .table {
    margin-top: 30px;
  }

  .pagination {
    margin-top: 10px;
    float: right;
  }

</style>



