<template>
    <div class="wrapper">
        <el-row class="containerAfter" v-if="flag">
            <el-col :span="4" class="menu">
                <db-sidebar></db-sidebar>
            </el-col>
            <el-col :span="20" class="content">
              <db-parent-info v-if="menu === 'parentInfo' && showFlag"></db-parent-info>
              <db-parent-stu v-if="menu === 'parentStu' && showFlag"></db-parent-stu>
              <db-teacher-info v-if="menu === 'teacher' && showFlag"></db-teacher-info>
            </el-col>
        </el-row>
        <el-row class="container" v-if="!flag">
          <el-col :span="8" :push="8">
            <el-form :model="user" ref="user" :rules="rules">
              <el-row>
                <el-col :span="4"style="margin-top: 10px">
                  username:
                </el-col>
                <el-col :span="12" :push="1">
                  <el-input v-model="user.userName"></el-input>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="4"  style="margin-top: 20px">
                  password:
                </el-col>
                <el-col :span="12" :push="1" style="margin-top: 10px">
                  <el-input v-model="user.password" type="password"></el-input>
                  <el-button @click="login" style="margin-top: 10px;float: right">login</el-button>
                </el-col>
              </el-row>
            </el-form>
          </el-col>
        </el-row>
    </div>
</template>

<script>
    import store from '@/store'
    import DbParentInfo  from './components/DbParentInfo.vue'
    import DbParentStu from './components/DbParentStu.vue'
    import DbSidebar from './components/DbSidebar.vue'
    import DbTeacherInfo from './components/DbTeacherInfo.vue'
    import ElRow from "element-ui/packages/row/src/row";
    import { mapState } from 'vuex'
    export default {
        name: 'app',
        components: {
          DbTeacherInfo,
          DbParentStu,
          DbParentInfo,
            ElRow,
            DbSidebar
        },
      computed: {
        ...mapState({
          menu: state => state.main.menu
        })
      },
      data () {
          return {
            user: {
              userName: 'admin',
              password: 'admin'
            },
            showFlag: false,
            flag: false,
            rules: {
                userName: [ { required: true, message: 'userName Required', trigger: 'blur' },
                  {min: 1, max: 20, message: '1~20'}],
              password: [ { required: true, message: 'password Required', trigger: 'blur' },
                {min: 1, max: 20, message: '1~20'}]
            }
          }
      },
      watch: {
        menu: function () {
          this.showFlag = false
          setTimeout(() => {
            this.showFlag = true
          }, 1)
        }
      },
      methods: {
        login () {
          this.$axios.post("http://localhost:8000/adminLogin", {name: this.user.userName, password: this.user.password}).then((response) => {
            if (response) {
              this.flag = true
              setTimeout(() => {
                this.showFlag = true
              }, 1)
              store.dispatch('setMenu', 'parentInfo')
              store.dispatch('setNum', 0)
            }
          })
        }
      }
    }

</script>

<style>

    element.style {
        background-color: rgb(10, 47, 88);
    }

    body {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        margin: 0;
        display: flex;
        min-height: 100vh;
        flex-direction: column;
    }

    .el-menu, body, html {
        height: 100%;
    }

    .wrapper {

        position: relative;
    }

    footer, div {
        display: block;
    }

    .container {
        padding-top: 150px;
        flex: 1;

    }

    .containerAfter {
      padding-top: 70px;
      flex: 1;

    }

    .container, .wrapper {
        height: 100%;
    }

    .menu {
        height: 100%;
        background-color: #eef1f6;
    }

    .content {
        padding-top: 25px;
        padding-right: 25px;
        padding-bottom: 125px;
        padding-left: 25px;
    }

    .footer {
        height: 120px;
        background-color: #324057;
        color: #a4aebd;
        width: 100%;
        z-index: 1000;
        position: absolute;
        bottom: 0;
        line-height: 1;
        font-size: 22px;
    }

</style>



