import Vue from 'vue'
Vue.filter('typeFormat', function (value) {
  let name = ''
  switch(value) {
    case 0: name = 'Order'; break
    case 1: name = 'Western food'; break
    case 2: name = 'Daily fee'; break
    case 3: name = 'Burger'; break
    case 4: name = 'Burger set'; break
    case 5: name = 'Dessert'; break
    case 6: name = 'Drink'; break
    case 7: name = 'Special offer'; break
  }
  return name
})



